@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap');

body{
    font-family: "Roboto", sans-serif;
    margin: 0;
    padding: 0;
}

.container{
    width: 1170px;
    max-width: 100%;
    margin: auto;
    padding: 0 20px;
    box-sizing: border-box;
}

.btn{
    padding: 10px 10px;
    border-radius: 5px;
    display: block;
    background: #00d98d;
    cursor: pointer;
    text-decoration: none;
    color: #fff;
    text-align: center;
}

p{
    font-size: 16px;
}

button{
    padding: 10px;
    background-color: #00d98d;
    color: #ffffff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 16px;
}

input, textarea{
    padding: 8px;
    box-sizing: border-box;
    margin-right: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
}

h1{
    font-size: 18px;
    font-weight: bold;
    margin-bottom: 10px;
}