.user-list__container {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    grid-gap: 20px;
    margin-top: 20px;
}

.user__wrapper{
    display: block;
    text-decoration: none;
    color: #000;
}

.user {
    background: #f0f0f0;
    padding: 10px;
    border-radius: 5px;
    height: 100%;
    box-sizing: border-box;
}

.user .item {
    margin-bottom: 5px;
}

.user .item:first-child {
    font-weight: bold;
}